                    self.personText = fileReader.result;
                };

                fileReader.readAsText(event.target.files[0])
            },
            handleItemFileChosen(event) {
                let fileReader = new FileReader();
                let self = this;
                fileReader.onload = event => {
                    self.itemText = fileReader.result;
                };

                fileReader.readAsText(event.target.files[0])
            },
            generatePerson() {
                let excludeNumberArray = [];
                this.personAutoGenData.excludeNumberText.split(",").forEach((value) => {
                    if (value.trim() !== "") {
                        excludeNumberArray.push(value);
                    }

                });
                this.personOriginList = [];
                for (let i = this.personAutoGenData.startNumber; i <= this.personAutoGenData.endNumber; i++) {

                    if (excludeNumberArray.indexOf(i.toString()) !== -1) {
                        continue;
                    }

                    let str = this.personAutoGenData.numberLeftPad ? leftPad(i, this.personAutoGenData.endNumber.toString().length, 0) : i;

                    str = this.personAutoGenData.prefix + str + this.personAutoGenData.suffix;

                    this.personOriginList.push({
                        name: str
                    });
                }

            },
            parsePersonText() {
                let persons = [];
                this.personText.split("\n").forEach((value) => {
                    if (value.trim() !== "") {
                        persons.push({
                            name: value
                        });
                    }

                });
                this.personOriginList = persons;
            },
            parseItemText() {
                let items = [];
                this.itemText.split("\n").forEach((value) => {
                    if (value.trim() !== "") {
                        let item = value.split(",");
                        items.push({
                            name: item[0],
                            number: item[1] * 1
                        })
                    }
                });
                this.itemOriginList = items;
            },
            fullscreen() {
                screenfull.request($("#luckyDrawMain")[0]);
            },
            initDrawData() {
                this.drawData.itemCurrentList = JSON.parse(JSON.stringify(this.itemOriginList));
                this.drawData.personCurrentList = JSON.parse(JSON.stringify(this.personOriginList));
                this.drawData.drawViewSourceList = JSON.parse(JSON.stringify(this.personOriginList));

                this.drawData.status = 'ready';

                this.drawData.showIndex = -1;

                this.settingNextDrawData();
            },
            settingNextDrawData() {
                if (this.drawData.personCurrentList.length === 0 || this.drawData.itemCurrentList.length === 0) {
                    this.drawData.status = 'end';
                } else {
                    if (this.drawType === 'drawPersonForItem') {
                        this.drawData.currentDrawTarget = this.drawData.itemCurrentList[this.drawData.itemCurrentList.length - 1];
                    }
                    this.drawData.status = 'ready';
                }
            },
            settingDrawView() {
                this.drawData.drawViewList = [];
                for (let i = 0; i < 20; i++) {
                    this.drawData.drawViewList.push(this.drawData.drawViewSourceList[this.getRandomNumber(this.drawData.drawViewSourceList.length)]);
                }
                this.drawData.status = 'draw';
            },
            draw() {
                let winIndex, item, person;

                if (this.drawType === 'drawPersonForItem') {
                    item = this.drawData.itemCurrentList.pop();
                    item.number--;
                    if (item.number > 0) {
                        this.drawData.itemCurrentList.push(item);
                    }
                }

                winIndex = this.getRandomNumber(this.drawData.personCurrentList.length);
                person = this.drawData.personCurrentList[winIndex];
                this.drawData.personCurrentList.splice(winIndex, 1);
                this.drawData.drawViewList.push(person);

                this.writeLog(item, person);
            },
            clickDrawBtn() {
                this.settingDrawView();
                this.draw();
                this.showDrawView();
            },
            writeLog(item, person) {
                this.logs.push({
                    item: item,
                    person: person,
                    time: (new Date).toLocaleString()
                });
            },
            showDrawView() {
                let self = this;
                let interval;
                let length = self.drawData.drawViewList.length;
                let winIndex = length - 1;
                interval = setInterval(function () {
                    self.drawData.showIndex = self.getRandomNumber(length);
                }, 100);
                setTimeout(function () {
                    clearInterval(interval);
                    self.drawData.showIndex = winIndex;
                    self.drawData.status = 'confirm';
                }, 3000);
            },
            getRandomNumber(max) {
                return Math.floor(Math.random() * max);
            },
            checkPersonIsWin(person) {
                return this.drawData.personCurrentList.findIndex(element => {
                    return person.name === element.name;
                }) === -1;
            },
            getItemLastNumber(item) {
                let foundItem = this.drawData.itemCurrentList.find(element => {
                    return item.name === element.name;
                });

                return foundItem ? foundItem.number : 0;
            }
        },
        computed: {
            reverseLogs() {
                return this.logs.reverse();
            }
        },
        watch: {},
        components: {},
        mounted() {

        }
    }
</script>

